@import "../style/pages/all";

.container {
    .services-content {
        margin: 0 120px;
        .services-content-title {
            h1 {
                font-size: 46px;
                margin-bottom: 20px;
            }

            .services-content-btn {
                display: flex;
                justify-content: space-between;

                p {
                    color: #93939A;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }

                input {
                    margin-right: 275px;
                    width: 250px;
                    height: 60px;
                    color: white;
                    background-color: #0067FF;
                    font-weight: bold;
                    font-size: 16px;
                    border: none;
                    border-radius: 12px;
                }
            }
        }
        .services-content-grid {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 100px;
            margin-top: 100px;

            .grid-item {
                border-radius: 12px;
                border: 1px solid #d4d4e4;

                .item-icon {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-align: center;
                    height: 200px;
                    width: 100%;
                    background-color: #d4d4e4;
                    border-top-left-radius: 12px;
                    border-top-right-radius: 12px;
                    border: 1px solid #d4d4e4;
                    margin-bottom: 30px;


                    svg {
                        color: white;
                        width: 100px;
                        height: 100px;
                    }
                }

                .grid-text {
                    margin: 30px;

                    h3 {
                        font-size: 24px;
                        margin-bottom: 20px;
                    }

                    p {
                        color: #93939A;
                        margin-bottom: 20px;
                    }

                    h4 {
                        color: #0067FF;
                        margin-bottom: 50px;
                    }
                }
            }
        }
    }
}